module.exports = "<div id=quiz-detail> <div class=\"wrapper flex-column\"> <div class=flex-row> <mdl-button class=icon-left-button raised primary @click=goBack()><i class=material-icons>keyboard_arrow_left</i>返回</mdl-button> <mdl-button v-if=quickquiz.finished style=margin-left:16px raised><i class=material-icons style=font-size:20px;margin-right:4px>redo</i>再測一次</mdl-button> <mdl-button v-if=!quickquiz.finished style=margin-left:16px raised accent @click=endQuiz()><i class=material-icons style=font-size:20px;margin-right:4px>gavel</i>結束小測</mdl-button> <mdl-button style=margin-left:16px raised primary v-link=\"{'name': 'quiz-paper', params: { quickquiz_id: $route.params.quickquiz_id }}\"><i class=material-icons style=font-size:20px;margin-right:4px>description</i>查看试卷</mdl-button> </div> <div class=\"flex-column second-wrapper mdl-shadow--2dp\"> <div class=flex-column style=padding:16px> <div class=\"flex-row flex-baseline\"> <h4 class=quiz-title>{{quickquiz.title}}</h4> <span class=quiz-finished>{{quickquiz.finished | finished}}</span> <span class=\"flex-row flex-center quiz-questions-count\"><i class=material-icons>description</i><span>{{quickquiz.questions}}題</span></span> <span class=\"flex-row flex-center quiz-time\"><i class=material-icons>timer</i><span>{{quickquiz.time}}分鐘</span></span> </div> <span class=quiz-starttime>開始於：{{quickquiz.startTime | date 'YYYY[年]M[月]DD[日] h:mm a'}}</span> <span class=quiz-finishtime v-if=quickquiz.finishTime>結束於：{{quickquiz.finishTime | date 'YYYY[年]M[月]DD[日] h:mm a'}}</span> </div> <div class=flex-column v-if=quickquiz.students> <p style=\"border-top:1px solid #eee;padding:8px 0 8px 0;margin:0;text-align:center;color:#aaa\">參與的學生</p> <div class=grids> <div class=\"grid-4 flex-column student\" v-for=\"student in quickquiz.students\" track-by=_id> <div class=\"flex-row flex-center\"> <div class=student-status :class=\"{'online': quickquiz.students[$index].online }\"> </div> <span class=student-name>{{student.name}}</span> </div